import React from 'react'
import { useCustomTranslation } from '../../translation'
import { CustomButton } from '../globalComponents'
const GiveFeedbackModal = (props) => {
    const { t } = useCustomTranslation()
    const { openModel, handleOpenClose, toggleTeamMemberModal } = props
    return (
        <>
        <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
            tabIndex={-1}
            style={{
                visibility: openModel ? 'visible' : 'hidden',
                width: "700px"
            }}
            id="offcanvasExampleLabel"
        >

            <div className="offcanvas-header">
                <h5 className="offcanvas-title  fs-6 fw-bold text-primary">Appraise</h5>
                <button type="button"
                    className="btn-close"
                    data-bs-dismiss="offcanvas"
                    aria-label="Close"
                    onClick={() => handleOpenClose()}
                />

            </div>
            <div className="offcanvas-body">
                <div className="formPanel">
                    <div className="form-group mb-3 d-flex align-items-center gap-4">
                        <label className="mb-0">Select Type</label>
                        <span className="toggleSwitch d-flex align-items-center gap-2 text-light">
                            Individual
                            <input
                                id="switchques1"
                                className="d-none"
                                type="checkbox"
                                defaultValue={0}
                                name="show_question_number"
                            />
                            <label htmlFor="switchques1" className="mb-0">
                                Toggle
                            </label>
                            Team
                        </span>
                    </div>
                    <div className="form-group mb-3">
                        <label htmlFor="" className="text-primary">
                            Select Recipient
                        </label>
                        <select className="form-select">
                            <option selected="">Select recipient</option>
                        </select>
                    </div>
                    <div className="form-group mb-3 d-flex align-items-center align-items-center gap-3">
                        <div className="w-75">
                            <label htmlFor="" className="text-primary">
                                Select Team
                            </label>
                            <select className="form-select">
                                <option selected="">Select Team</option>
                            </select>
                        </div>
                        <CustomButton
                            classNames="btn btn-primary gradientBg mt-auto"
                            text={t("btns.createTeam")}
                            handleClick={() => toggleTeamMemberModal()}
                        />
                        {/* <button
                            type="button"
                            className="btn btn-primary gradientBg mt-auto"
                            data-bs-target="#selectTeam"
                            data-bs-toggle="modal"
                        >
                            Create Team
                        </button> */}
                    </div>
                    <div className="form-group mb-3">
                        <label htmlFor="" className="text-primary">
                            Write Context
                        </label>
                        <input
                            type="text"
                            name=""
                            id=""
                            className="form-control"
                            placeholder="Enter here.."
                        />
                    </div>
                    <div className="form-group mb-3">
                        <span className="toggleSwitch d-flex align-items-center gap-2 text-primary fw-semibold">
                            <input
                                id="switchques3"
                                className="d-none"
                                type="checkbox"
                                defaultValue={0}
                                name="show_question_number"
                            />
                            <label htmlFor="switchques3" className="mb-0">
                                Toggle
                            </label>
                            Give Rating( if Required)
                        </span>
                    </div>
                    <div className="form-group mb-3 d-flex gap-3 align-items-center">
                        <div className="d-flex gap-2 ">
                            <img src="images/fil-star.svg" alt="" width={20} />
                            <img src="images/fil-star.svg" alt="" width={20} />
                            <img src="images/fil-star.svg" alt="" width={20} />
                            <img src="images/fil-star.svg" alt="" width={20} />
                            <img src="images/fil-star.svg" alt="" width={20} />
                        </div>
                        <span>OR</span>
                        <ul className="emojilable">
                            <li>
                                <input id="npsQ1" className="d-none" type="radio" name="npsQ" />
                                <label htmlFor="npsQ1">1</label>
                            </li>
                            <li>
                                <input id="npsQ2" className="d-none" type="radio" name="npsQ" />
                                <label htmlFor="npsQ2">2</label>
                            </li>
                            <li>
                                <input id="npsQ3" className="d-none" type="radio" name="npsQ" />
                                <label htmlFor="npsQ3">3</label>
                            </li>
                            <li>
                                <input id="npsQ4" className="d-none" type="radio" name="npsQ" />
                                <label htmlFor="npsQ4">4</label>
                            </li>
                            <li>
                                <input id="npsQ5" className="d-none" type="radio" name="npsQ" />
                                <label htmlFor="npsQ5">5</label>
                            </li>
                        </ul>
                    </div>
                    <div className="form-group mb-3">
                        <label htmlFor="" className="text-primary">
                            Enter Feedback
                        </label>
                        <textarea className="form-control" defaultValue={""} />
                    </div>
                    <div className="form-group mb-3 d-flex align-items-center gap-3">
                        <div className="acceptCheck">
                            <input
                                type="radio"
                                id="generalRadio"
                                className="d-none"
                                name="generalRadio"
                            />
                            <label htmlFor="generalRadio">General</label>
                        </div>
                        <div className="acceptCheck">
                            <input
                                type="radio"
                                id="kpiWiseRadio"
                                className="d-none"
                                name="generalRadio"
                            />
                            <label htmlFor="kpiWiseRadio">KPI Wise</label>
                        </div>
                    </div>
                    <div className="form-group mb-3">
                        <ul>
                            <li>
                                <div className="form-check">
                                    <input
                                        type="checkbox"
                                        className="form-check-input"
                                        id="kpicheck1"
                                    />
                                    <label
                                        htmlFor="kpicheck1"
                                        className="form-check-label fw-medium text-black"
                                    >
                                        KPI 1 (KPI Name)
                                    </label>
                                </div>
                            </li>
                            <li>
                                <div className="form-check">
                                    <input
                                        type="checkbox"
                                        className="form-check-input"
                                        id="kpicheck2"
                                    />
                                    <label
                                        htmlFor="kpicheck2"
                                        className="form-check-label fw-medium text-black"
                                    >
                                        KPI 2 (KPI Name)
                                    </label>
                                </div>
                            </li>
                            <li>
                                <div className="form-check">
                                    <input
                                        type="checkbox"
                                        className="form-check-input"
                                        id="kpicheck3"
                                    />
                                    <label
                                        htmlFor="kpicheck3"
                                        className="form-check-label fw-medium text-black"
                                    >
                                        KPI 3 (KPI Name)
                                    </label>
                                </div>
                            </li>
                            <li>
                                <div className="form-check">
                                    <input
                                        type="checkbox"
                                        className="form-check-input"
                                        id="kpicheck4"
                                    />
                                    <label
                                        htmlFor="kpicheck4"
                                        className="form-check-label fw-medium text-black"
                                    >
                                        KPI 4 (KPI Name)
                                    </label>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
            <div className="offcanvas-footer">
                <div className="d-flex align-items-center justify-content-end gap-3">
                    <a
                        href="#approvelmsg"
                        data-bs-toggle="modal"
                        className="btn btn-primary gradientBg"
                    >
                        Submit
                    </a>
                    <a href="#" className="btn btn-secondary">
                        Back
                    </a>
                </div>
            </div>
        </div>
        {openModel && <div className="modal-backdrop fade show"></div>}
        </>

    )
}

export default GiveFeedbackModal
