import React from 'react'
import { CustomTabs } from '../reuseableComponents'
import { CustomSpinner } from '../globalComponents'
import BlankTableError from '../commanComponent/BlankTableError'
const receivedFeedbackData = [{ name: 'Mohd Sarfaraz' }]
const ReceiveFeedback = (props) => {
    const { receivedFeedbackTabs, toggleFeedbackKudosModal, isLoading = false } = props


    const renderReceivedFeedback = () => {
        return (
            receivedFeedbackData?.length > 0 ? receivedFeedbackData?.map(() =>
                <li>
                    <div
                        onClick={() => { toggleFeedbackKudosModal("Feedback") }}
                        className="subCategry"
                    >
                        <div className="row">
                            <div className="col-md-9 border-end">
                                <div className="d-flex gap-3">
                                    <figure className="mb-0">
                                        <img src="images/profile.png" alt="" width={40} />
                                    </figure>
                                    <div>
                                        <span>Mohd Sarfaraz</span>
                                        <p>React JS Developer</p>
                                    </div>
                                </div>
                                <strong className="pt-2">Feedback</strong>
                                <p>
                                    Lorem Ipsum is simply dummy text of the printing and
                                    typesetting industry. Lorem Ipsum has been the industry's
                                    standard dummy text ever since the 1500s, when an unknown
                                    printer took a galley of type and scrambled it to make a
                                    type specimen book.
                                </p>
                            </div>
                            <div className="col-md-3">
                                <div className="d-flex flex-wrap justify-content-center h-100 text-center ">
                                    <p className="w-100 mb-auto mt-2">04 Sep, 2024</p>
                                    <div className="mt-auto mb-4">
                                        <img src="images/arrow-right.svg" width={35} />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </li>
            )
                :
                <div className='d-flex justify-content-center'>
                    <BlankTableError />
                </div>
        )
    }

    const renderTeamLeaderFeedback = () => {
        return (
            receivedFeedbackData?.length > 0 ? receivedFeedbackData?.map(() =>
                <li>
                    <div 
                    // onClick={() => toggleFeedbackKudosModal()}
                     className="subCategry">
                        <div className="row">
                            <div className="col-md-9 border-end">
                                <div className="d-flex gap-3">
                                    <figure className="mb-0">
                                        <img src="images/profile.png" alt="" width={40} />
                                    </figure>
                                    <div>
                                        <span>Abhay Sharma</span>
                                        <p>Finance Manager</p>
                                    </div>
                                </div>
                                <strong className="pt-2">Feedback Context</strong>
                                <p>Great leadership and performance in quarter 3</p>
                            </div>
                            <div className="col-md-3">
                                <div className="d-flex flex-wrap justify-content-center h-100 text-center ">
                                    <div className="text-center">
                                        <span>Scored / Scorable</span>
                                        <b>
                                            4 <span>/ 5</span>
                                        </b>
                                    </div>
                                    <p className="w-100 mt-auto text-end">04 Sep, 2024</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </li>
            )
                :
                <div className='d-flex justify-content-center'>
                    <BlankTableError />
                </div>
        )
    }
    const renderManagerFeedback = () => {
        return (
            receivedFeedbackData?.length > 0 ? receivedFeedbackData?.map(() =>
                <li>
                    <div
                    // onClick={() => toggleFeedbackKudosModal()} 
                    className="subCategry">
                        <div className="row">
                            <div className="col-md-9 border-end">
                                <div className="d-flex gap-3">
                                    <figure className="mb-0">
                                        <img src="images/profile.png" alt="" width={40} />
                                    </figure>
                                    <div>
                                        <span>Abhay Sharma</span>
                                        <p>Finance Manager</p>
                                    </div>
                                </div>
                                <strong className="pt-2">Feedback Context</strong>
                                <p>Great leadership and performance in quarter 3</p>
                            </div>
                            <div className="col-md-3">
                                <div className="d-flex flex-wrap justify-content-center h-100 text-center ">
                                    <div className="text-center">
                                        <span>Scored / Scorable</span>
                                        <b>
                                            4 <span>/ 5</span>
                                        </b>
                                    </div>
                                    <p className="w-100 mt-auto text-end">04 Sep, 2024</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </li>
            )
                :
                <div className='d-flex justify-content-center'>
                    <BlankTableError />
                </div>
        )
    }


    return (
        <div className="tab-pane fade show active" id="receivedFeedback">
            <div className="cardBox myRequest mb-3">
                <div className="cardHead d-flex justify-content-between">
                    <div className="d-flex align-items-center">
                        <img
                            src="images/kpi-guidline.svg"
                            alt=""
                            width={40}
                            className="me-2 fw-bold"
                        />
                        <div className="">
                            <h3> Received Feedback</h3>
                        </div>
                    </div>
                    <div className="searchB me-0 ">
                        <input
                            type="text"
                            className="form-control w-100 fs-7"
                            placeholder="Search"
                        />
                    </div>
                </div>
                <CustomTabs tabs={receivedFeedbackTabs} type="receivedFeedback" />
                <div className="tab-content mykpiMain">
                    <div className="tab-pane fade active show" id="all">
                        <ul className="subCategoryUl">
                            {isLoading ?
                                <div className='d-flex justify-content-center m-5'>
                                    <CustomSpinner isShow={isLoading} size="md" />
                                </div>
                                :
                                renderReceivedFeedback()
                            }
                        </ul>
                    </div>
                    <div className="tab-pane fade" id="teamLeader">
                        <ul className="subCategoryUl">
                            {isLoading ?
                                <div className='d-flex justify-content-center m-5'>
                                    <CustomSpinner isShow={isLoading} size="md" />
                                </div>
                                :
                                renderTeamLeaderFeedback()
                            }

                        </ul>
                    </div>
                    <div className="tab-pane fade" id="manager">
                        <ul className="subCategoryUl">
                            {isLoading ?
                                <div className='d-flex justify-content-center m-5'>
                                    <CustomSpinner isShow={isLoading} size="md" />
                                </div>
                                :
                                renderManagerFeedback()
                            }
                        </ul>
                    </div>
                    <div className="tab-pane fade" id="functionalHead">
                    <ul className="subCategoryUl">
                            {isLoading ?
                                <div className='d-flex justify-content-center m-5'>
                                    <CustomSpinner isShow={isLoading} size="md" />
                                </div>
                                :
                                renderManagerFeedback()
                            }
                        </ul>
                    </div>
                </div>
            </div>
        </div>

    )
}

export default ReceiveFeedback
